body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.App-body {
  display: flex;
}


.line-item {
    justify-content: space-around;
    display: flex;
    width: 95% ;
    height: 40px;
    border: 2px solid #4FB3E5;
    border-radius: 30px;
    margin-top: 10px;
    margin-left: 2%;

    box-shadow: 2px 3px 5px rgba(0,0,0,.30);
}

.line-left,.line-right, .line-left:focus {
    background-color: transparent;
    border: none;
    width: 100% ;
    height: 70%;
    border-right: 2px solid #4FB3E5;
    border-color: #4FB3E5;
    color: #4FB3E5;
    padding: 6px 0px;
    margin-left: 10px;
    outline: none;
    font-size: 18px;
    text-align: center;
}
.line-info {
    width: 50% ;
    display: flex;
    justify-content: center;
}
.line-button {
    margin-top: 4px;
    margin-left: 8px;
    background-color: transparent;
    border-color: transparent;
    cursor: pointer;
    outline: none;
}
.line-warning {
    margin-top: 4px;
    margin-left: 18px;
    background-color: transparent;
    border-color: transparent;
    
}
.table-list {
    height: 350px;
    overflow: auto;
    border-bottom: 2px solid #4FB3E5;
    border-radius: 5px;
}
.table {
    width: 450px;
    height: 410px;
    border: 2px solid;
    border-color: #4FB3E5;
    border-radius: 20px;
    
    margin-left: 5px;
}
.table-bottom {
    justify-content: space-around;
    display: flex;
    width: 95% ;
    height: 40px;
    border: 2px solid #4FB3E5;
    border-radius: 30px;
    margin-top: 1.5%;
    margin-left: 2%;
    
    box-shadow: 2px 3px 5px rgba(0,0,0,.30);
}
.table-input {
    background-color: transparent;
    border: none;
    width: 100% ;
    height: 70%;
    border-right: 2px solid;
    border-color: #4FB3E5;
    color: #4FB3E5;
    padding: 6px 0px;
    margin-left: 2%;
    outline: none;
    font-size: 18px;
    text-align: center;
}
.table-button {
    background-color: transparent; 
    border: none;
    color: #ccc;
    padding: 12px 10px;
    cursor: pointer;
    text-align: center;
    touch-action: manipulation;
    outline: none;
    
}

.validate-button {
    outline: none;
    border: none;
    cursor: pointer;
    background-color: transparent;
    align-self: flex-end;
    color: #4FB3E5;
    font-size: 18px;
}

.table-header {
    justify-content: space-around;
    display: flex;
    
}
.Dictionaries {
    justify-content: center;
    width: 200px;
    height: 410px;
    border: 2px solid #4FB3E5;
    border-radius: 20px;
}

.dictionary-list{
   align-items: center;
    width: 200px;
    height: 350px;
    overflow: auto;
    border-bottom: 2px solid #4FB3E5;
}

.Dictionary {
    align-items: center;
    justify-content: space-around;
    display: flex;
    width: 90% ;
    height: 40px;
    border: 2px solid #4FB3E5;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 10px;
    box-shadow: 2px 3px 5px rgba(0,0,0,.30);
}

.dictionary-button {
    align-items: center;
    justify-content: space-around;
    display: flex;
    width: 90% ;
    height: 40px;
    color: #fff;
    font-size: 18px;
    border: 2px solid #4FB3E5;
    border-radius: 20px;
    background-color: transparent;
    border-color: transparent;
    outline: none;
    margin-left: 10px;
}

.dictionary-input {
    
    background-color: transparent;
    border: none;
    width: 100% ;
    height: 70%;
    border-right: 2px solid #4FB3E5;
    color: #fff;
    padding: 6px 0px;
    margin-left: 2%;
    outline: none;
    font-size: 18px;
    text-align: center;
}

